.contact {
  width: 100%;
  height: auto;
}

.contact__header {
  margin: 0;
  padding: 0;
  margin-top: 30px;
  margin-bottom: 20px;
  font-family: "Playfair Display";
  font-weight: normal;
  font-size: 36px;
  text-align: center;

  @media (min-width: $viewport-mid) {
    margin: 40px 0 30px;
    font-size: 44px;
  }    
}

.contact__input {
  width: 100%;
  height: 40px;
  margin: 0;
  padding: 0;
  padding-left: 10px;
  font-family: "Lato";
  font-size: 11px;
  border: transparent;
  border-bottom: 2px solid $grey;

  @media (min-width: $viewport-mid) {
    height: 50px;
    font-size: 13px;
  }    
}

.contact__textarea {
  width: 100%;
  height: 100px;
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-left: 10px;
  font-family: "Lato";
  font-size: 11px;
  border: transparent;
  border-bottom: 2px solid $grey;

  @media (min-width: $viewport-mid) {
    height: 130px;
    font-size: 13px;
  }    
}

.contact__btn {
  margin: 0;
  padding: 0;
  margin-left: calc(44vw - 80px);
  margin-top: 30px;
  margin-bottom: 20px;
  width: 160px;
  height: 40px;
  font-family: "Lato";
  font-size: 14px;
  letter-spacing: 0.05em;
  line-height: 18px;
  text-transform: uppercase;
  color: $white;
  border: none;
  background-color: $coral;

  @media (min-width: $viewport-mid) {
    margin-left: calc(35vw - 80px);
    margin-top: 40px;
    margin-bottom: 35px;
  }    
}
